.hero {
    padding-bottom: 80px;
    overflow: hidden;
  }
  
  @media screen and (max-width: 767px) {
    .title {
      margin-bottom: 48px;
    }
  }
  
  @media screen and (min-width: 768px) and (max-width: 1280px) {
    .title {
      margin-bottom: 64px;
    }
  }
  
  .title-hero {
    margin-bottom: 14px;
    color: var(--black-second-color);
    font-family: Inter, sans-serif;
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2;
    text-transform: uppercase;
  }
  
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    .title-hero {
      max-width: 446px;
      margin-bottom: 16px;
      font-size: 36px;
    }
  }
  
  @media screen and (min-width: 1280px) {
    .title-hero {
      min-width: 446px;
      margin-bottom: 16px;
      font-size: 36px;
    }
  }
  
  .title-hero-color {
    color: var(--breen-color);
    font-family: Inter, sans-serif;
    font-size: 26px;
    font-style: italic;
    font-weight: 400;
    line-height: 1.2;
    text-transform: uppercase;
  }
  
  @media screen and (min-width: 768px) {
    .title-hero-color {
      font-size: 36px;
    }
  }
  
  .text-hero {
    margin-bottom: 28px;
    color: var(--black-second-color);
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.29;
  }
  
  @media screen and (min-width: 768px) {
    .text-hero {
      font-size: 16px;
      line-height: 1.5;
      max-width: 408px;
      margin-bottom: 32px;
    }
  }
  
  .btn-hero {
    display: inline-flex;
    padding: 10px 20px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 12px;
    border: 1px solid var(--breen-color);
    color: var(--black-second-color);
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  
  @media screen and (min-width: 768px) {
    .btn-hero {
      padding: 14px 28px;
      border-radius: 15px;
    }
  }
  
  .btn-hero:hover,
  .btn-hero:focus {
    background-color: var(--breen-color);
    color: var(--white-second-color);
  }
  