h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-family: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 0;
  font-family: inherit;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

textarea {
  resize: none;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f8f8f8;
}

::-webkit-scrollbar-thumb {
  background: var(--breen-color);
  border: 2px solid #f8f8f8;
  border-radius: 5px;
}

.header {
  color: var(--black-second-color);
  align-content: center;
  padding: 20px 0;
}

.header-container {
  align-items: center;
  display: flex;
}

.navigation {
  display: none;
}

.nav-item {
  align-items: center;
  display: flex;
}

.nav-item-link {
  color: var(--black-second-color);
  transition: color var(--time) var(--cubic);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.nav-item-link:hover, .nav-item-link:focus {
  color: var(--breen-color);
}

.nav-item:not(:last-child) {
  margin-right: 16px;
}

.index .a, .favorite .b {
  color: var(--breen-color);
}

.logo {
  letter-spacing: -.64px;
  color: var(--black-second-color);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.logo-link {
  margin-right: auto;
}

.logo-green {
  color: var(--breen-color);
}

.basket-button {
  background-color: #0000;
  align-items: center;
  margin-right: 14px;
  display: flex;
}

.basket-icon {
  fill: none;
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubic);
}

.basket-icon:hover, .basket-icon:focus {
  stroke: var(--breen-color);
}

.menu-container {
  z-index: 9;
  background-color: var(--breen-color);
  transition: transform var(--time) var(--cubic);
  width: 200px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  z-index: 9;
  background-color: var(--breen-color);
  transition: transform var(--time) var(--cubic);
  width: 200px;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0);
}

.menu-toggle-icon {
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubic);
}

.menu-toggle-icon-close {
  stroke: var(--white-second-color);
  transition: stroke var(--time) var(--cubic), transform var(--time) var(--cubic);
}

.menu-toggle-icon:hover, .menu-toggle-icon:focus {
  stroke: var(--breen-color);
}

.menu-toggle-icon-close:hover, .menu-toggle-icon-close:focus {
  stroke: var(--black-second-color);
  transform: rotate(180deg);
}

.menu-toggle-open, .menu-toggle-close {
  background-color: #0000;
  align-items: center;
  display: flex;
}

.menu-toggle-close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-list {
  flex-direction: column;
  align-items: center;
  margin-top: 200px;
  display: flex;
}

.mob-menu-item:not(:last-child) {
  margin-bottom: 16px;
}

.mob-menu-link {
  color: var(--white-second-color);
  transition: color var(--time) var(--cubic);
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;
}

.mob-menu-link:hover, .mob-menu-link:focus {
  color: var(--black-second-color);
}

.header-modal {
  background-color: var(--white-second-color);
  z-index: 9;
  border-radius: 15px;
  width: 295px;
  height: 580px;
  margin: 0 auto;
  padding: 28px 20px;
  position: relative;
}

.header-modal-title {
  color: var(--black-second-color);
  text-transform: uppercase;
  margin-bottom: 28px;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
}

.header-form {
  text-align: left;
}

.header-form-label {
  color: #05050580;
  margin-bottom: 8px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.header-form-input {
  border: 1px solid #05050580;
  border-radius: 15px;
  align-items: flex-start;
  width: 100%;
  height: 46px;
  margin-bottom: 16px;
  padding: 14px 18px;
}

.header-form-comment {
  height: 100px;
  margin-bottom: 40px;
}

.header-form-btn {
  color: #f8f8f8;
  text-align: center;
  background: var(--breen-color);
  border-radius: 15px;
  width: 100%;
  height: 46px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.modal-close-btn:hover, .modal-close-btn:focus {
  stroke: var(--breen-color);
  transform: rotate(180deg);
}

.modal-close-btn {
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubic), transform var(--time) var(--cubic);
  background-color: #0000;
  background-image: url("blackx.cb0486d6.svg");
  background-position: center;
  background-size: cover;
  align-items: center;
  width: 20px;
  height: 20px;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  .navigation {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .menu-toggle-open {
    display: none;
  }

  .logo-link {
    margin: 0 auto;
  }

  .logo {
    font-size: 18px;
  }

  .basket-link {
    margin-right: 18px;
  }

  .header {
    padding: 28px 0;
  }

  .header-modal {
    width: 440px;
    height: 632px;
    padding: 40px;
  }

  .header-form-input {
    height: 48px;
  }

  .modal-close-btn {
    width: 24px;
    height: 24px;
    top: 16px;
    right: 16px;
  }

  .modal-icon-close {
    width: 24px;
    height: 24px;
  }

  .header-modal-title {
    color: #050505;
    text-transform: uppercase;
    margin-bottom: 40px;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px;
  }

  .header-form-input {
    margin-bottom: 18px;
  }

  .header-form-comment {
    height: 100px;
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    padding: 40px 0;
  }
}

.switcher-check-text {
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .switcher-wrapper {
    display: none;
  }
}

.switcher-check-text:before {
  content: "";
  transition: background-color var(--time) var(--cubic);
  background-color: #cecdcd;
  border-radius: 50px;
  justify-content: center;
  width: 46px;
  height: 20px;
  display: flex;
  box-shadow: 7px 5px 15px #a09ea099;
}

.switcher-check-text:after {
  content: "";
  background-color: var(--white-second-color);
  transition: background-color var(--time) var(--cubic), transform var(--time) var(--cubic);
  border-radius: 50%;
  width: 18px;
  height: 18px;
  position: absolute;
  left: 1px;
}

.switcher-check:checked + .switcher-check-text:after {
  background-color: var(--whiit-first-color);
  transform: translateX(26px);
}

.switcher-check:checked + .switcher-check-text:before {
  background-color: var(--breen-color);
}

